import nebulas from "nebulas";
import nebpayCls from "nebpay.js";
import Vue from 'vue';

//nas
var nasConfig = {
  name: "",
  contractAddress: "n1psFap63qHc4QHbAK1ip2zrL8BTJ9DgcHq",
  host: "https://mainnet.nebulas.io",
};

//
var neb = new nebulas.Neb();
neb.setRequest(new nebulas.HttpRequest(nasConfig.host));
var nebApi = neb.api;
var nebPay = new nebpayCls();

class NasTool {
  static init() {
    this.initAddress();
    nebApi.getNebState().then((resp) => {
      this.chain_id = resp.chain_id;
      /*app.$hub.nebState = resp
      app.$hub.nebState.chain_id = resp.chain_id*/
      console.log(resp)
    }).catch((error) => {
      alert("");
    });
  };

  //
  static initAddress() {
    var cur = this;
    if (this.isPc()) {
      window.addEventListener('message', function (e) {
        var d = e.data || {};
        if (d.type == 'webpackOk') {
          return;
        }
        if (d.data) {
          cur.myAddress = d.data.account || null;
          localStorage.setItem('myAddress', cur.myAddress || '');
        }
      });
      window.postMessage({
        "target": "contentscript",
        "data": {},
        "method": "getAccount",
      }, "*");
    } else {
      var myAddress = localStorage.getItem('myAddress');
      cur.myAddress = myAddress || null;
    }
  }

  //pc
  static isPc() {
    var userAgentInfo = navigator.userAgent;
    var Agents = ["Android", "iPhone",
      "SymbianOS", "Windows Phone",
      "iPad", "iPod"];
    var flag = true;
    for (var v = 0; v < Agents.length; v++) {
      if (userAgentInfo.indexOf(Agents[v]) > 0) {
        flag = false;
        break;
      }
    }
    return flag;
  }

  //nas
  static get(conf, success) {
    nebApi.call({
      chainID: this.chain_id,
      from: conf.from || nasConfig.contractAddress,
      to: nasConfig.contractAddress,
      value: conf.value || 0,
      // nonce: nonce,
      gasPrice: conf.gasPrice || 1000000,
      gasLimit: conf.gasLimit || 2000000,
      contract: {
        function: conf.func,
        args: JSON.stringify(conf.args)
      }
    }).then(resp => {
      success(resp);
      /*if (resp.execute_err && resp.execute_err != 'insufficient balance') {
        app.$refs.app.toast(resp.execute_err, 2000)
        throw new Error(resp.execute_err)
      }
      var result = JSON.parse(resp.result)
      return result*/
    }).catch(res => {
      this.hideLoading('');
    });
  }

  static pay(config, callback = () => {
  }) {
    var cur = this;
    var serialNumber; //
    var intervalQuery; //

    //, 
    var to = nasConfig.contractAddress;   //Dapp
    var value = config.value || 0;
    var callFunction = config.func; //
    var callArgs = config.args ? JSON.stringify(config.args) : '';  //JSON, '["arg"]','["arg1","arg2]'
    var options = {
      /*goods: {        //
        name: "example"
      },*/
      listener(v) {
        if (typeof v == 'string') {
          cur.hideLoading('');
          callback({code: -2});
        } else if (v.txhash) {
          cur.showloading('');
        }
      }
      //callback: NebPay.config.mainnetUrl //()
    };

    this.showloading('....');
    //()
    serialNumber = nebPay.call(to, value, callFunction, callArgs, options);

    //
    intervalQuery = setInterval(funcIntervalQuery, 10000); //10-15s,15s,10

    //. queryPayInfoPromise.
    function funcIntervalQuery() {
      //queryPayInfooptions,(,)
      nebPay.queryPayInfo(serialNumber, options)   //search transaction result from server (result upload to server by app)
        .then(function (resp) {
          console.log("tx result: " + resp)   //resp is a JSON string
          var respObject = JSON.parse(resp)
          //code==0, status==1
          if (respObject.code === 0) {
            var status = respObject.data.status;
            if (status == '2') {
              cur.showloading('');
              return;
            } else if (status == '1') {
              cur.hideLoading('...');
              clearInterval(intervalQuery);
              //
              cur.myAddress = respObject.data.from;
              callback({code: 0, data: respObject.data.execute_result || null});
            } else {
              clearInterval(intervalQuery)
              cur.hideLoading('...');
              callback({code: -1});
            }
          }
        }).catch(function (err) {
        console.log(err);
      });
    }
  }

  static showloading(msg) {
    Vue.$vux.loading.show({
      text: msg || '....'
    });
  }

  static hideLoading(msg) {
    Vue.$vux.loading.hide();
    if (msg) {
      Vue.$vux.toast.text(msg);
    }
  }
}

export default NasTool;




